import './AdminStaffUpdate.css'

import { IonSpinner } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { RouteComponentProps } from 'react-router'

import { Page } from '../../components'
import { useStores } from '../../models/root-store/root-store-context'

const defaultValues = {
  id: '',
  first_name: '',
  last_name: '',
  email: '',
  nro_telefono: '',
  nro_documento: '',
  razon_social: '',
  ruc: '',
  contrasena: '',
  repetir_contrasena: '',
  imagen_perfil: null,
  fabricante_id: null,
}

export const AdminStaffUpdate: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { adminStore } = rootStore
  const [Loading, setLoading] = useState(false)
  const [Errors, setErrors] = useState(defaultValues)
  const [Datos, setDatos] = useState(defaultValues)
  const [Origen, setOrigen] = useState('')
  const [FabricanteId, setFabricanteId] = useState(0)

  const setup = () => {
    if (history.location.hasOwnProperty('origen')) {
      // @ts-ignore
      setOrigen(history.location.origen)
    } else {
      setOrigen('staff')
    }
    if (history.location.hasOwnProperty('origen')) {
      // @ts-ignore
      setFabricanteId(history.location.fabricante_id)
    } else {
      setFabricanteId(0)
    }

    if (adminStore.clienteDetalle) {
      // @ts-ignore
      setDatos(adminStore.clienteDetalle)
    } else {
      setDatos(defaultValues)
    }
  }

  React.useEffect(() => {
    setup()
  }, [])

  const setDato = (campo: string, valor: string) => {
    let datos = { ...Datos }
    // @ts-ignore
    datos[campo] = valor
    setDatos(datos)
  }

  const validar_campos = () => {
    setErrors(defaultValues)
    let errors = { ...Errors }
    const reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
    let error = false
    if (Datos.email.trim() === '' && reg.test(Datos.email) === false) {
      errors['email'] = 'Ingrese una dirección de correo electrónico'
      error = true
    } else if (reg.test(Datos.email) === false) {
      errors['email'] = 'Ingrese una dirección de correo electrónico válida'
      error = true
    } else {
      errors['email'] = ''
    }

    if (Datos.first_name.trim() === '') {
      errors['first_name'] = 'Ingrese un nombre'
      error = true
    } else {
      errors['first_name'] = ''
    }

    if (Datos.last_name.trim() === '') {
      errors['last_name'] = 'Ingrese un apellido'
      error = true
    } else {
      errors['last_name'] = ''
    }

    if (!adminStore.clienteDetalle) {
      if (Datos.contrasena.trim() === '') {
        errors['contrasena'] = 'Se requiere una contraseña'
        error = true
      }
    }

    if (Datos.repetir_contrasena && Datos.contrasena !== Datos.repetir_contrasena) {
      errors['contrasena'] = 'Las contraseñas no coinciden'
      error = true
    }

    setErrors(errors)
    return !error
  }

  const guardar = () => {
    setLoading(true)
    setErrors(defaultValues)
    const datos = { ...Datos, fabricante_id: FabricanteId, username: Datos.email }
    const validacion = validar_campos()
    if (validacion) {
      rootStore.environment.api.guardarUsuarioStaff(datos).then((result: any) => {
        setLoading(false)
        if (result.kind === 'ok') {
          history.goBack()
          setDatos(defaultValues)
        } else if (result.kind === 'bad-data') {
          setErrors(result.errors)
          if (result.errors.non_field_errors) {
            alert(result.errors.non_field_errors[0])
          }
        } else {
          alert('Ocurrió un error al guardar vuelva a intentar en unos momentos')
        }
      })
    } else {
      setLoading(false)
    }
  }
  return (
    <Page login_required admin solo_admin>
      <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
        <div className="container-fluid background-white">
          <div className="row page-titles">
            <div className="col-md-5 col-12 align-self-center">
              {Origen && Origen !== 'fabricante' ? (
                <h3 className="text-themecolor">{Datos.id ? 'Editar usuario staff' : 'Crear usuario staff'}</h3>
              ) : (
                <h3 className="text-themecolor">{Datos.id ? 'Editar usuario proveedor' : 'Crear usuario proveedor'}</h3>
              )}
            </div>
            <div className="col-md-7 col-12 align-self-center d-none d-md-block">
              <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                <li className="breadcrumb-item">
                  <a onClick={() => history.push('/admin/')}>Inicio</a>
                </li>
                {Origen && Origen !== 'fabricante' ? (
                  <li className="breadcrumb-item">
                    <a href="/admin/staff">Usuarios Staff</a>
                  </li>
                ) : (
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Proveedor</a>
                  </li>
                )}
                <li className="breadcrumb-item active">{Datos.id ? 'Editar' : 'Crear'}</li>
              </ol>
            </div>
          </div>
          <div className="row">
            <div className="col-12">
              <div className="card">
                <div className="card-body">
                  <div className="row">
                    <div className="col-lg-6 col-md-6 col-sm-12">
                      <div className={'form-group'}>
                        <label id={'nombre'}>Nombre*</label>
                        <input
                          type={'text'}
                          className={'form-control'}
                          id={'nombre'}
                          value={Datos.first_name}
                          onChange={(evt) => setDato('first_name', evt.target.value)}
                        />
                        {Errors.first_name && <div className={'text-danger'}>{Errors.first_name}</div>}
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12">
                      <div className={'form-group'}>
                        <label id={'apellido'}>Apellido*</label>
                        <input
                          type={'text'}
                          className={'form-control'}
                          id={'apellido'}
                          value={Datos.last_name}
                          onChange={(evt) => setDato('last_name', evt.target.value)}
                        />
                        {Errors.last_name && <div className={'text-danger'}>{Errors.last_name}</div>}
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12">
                      <div className={'form-group'}>
                        <label id={'email'}>Correo electrónico*</label>
                        <input
                          type={'text'}
                          className={'form-control'}
                          id={'email'}
                          value={Datos.email}
                          onChange={(evt) => setDato('email', evt.target.value)}
                        />
                        {Errors.email && <div className={'text-danger'}>{Errors.email}</div>}
                      </div>
                    </div>
                  </div>
                  <div className="row">
                    <div className="col-lg-6 col-md-6 col-sm-12">
                      <div className={'form-group'}>
                        <label id={'contrasena'}>Contraseña{adminStore.clienteDetalle ? '' : '*'}</label>
                        <input
                          type={'password'}
                          className={'form-control'}
                          id={'contrasena'}
                          onChange={(evt) => setDato('contrasena', evt.target.value)}
                        />
                        {Errors.contrasena && <div className={'text-danger'}>{Errors.contrasena}</div>}
                      </div>
                    </div>
                    <div className="col-lg-6 col-md-6 col-sm-12">
                      <div className={'form-group'}>
                        <label id={'repetir_contrasena'}>
                          Repetir contraseña{adminStore.clienteDetalle ? '' : '*'}
                        </label>
                        <input
                          type={'password'}
                          className={'form-control'}
                          id={'repetir_contrasena'}
                          onChange={(evt) => setDato('repetir_contrasena', evt.target.value)}
                        />
                        {Errors.repetir_contrasena && <div className={'text-danger'}>{Errors.repetir_contrasena}</div>}
                      </div>
                    </div>
                  </div>
                  <div className={'form-group text-right'}>
                    <a onClick={() => history.goBack()} className={'btn btn-light'}>
                      Cancelar
                    </a>
                    <button className={'btn btn-primary ml-2'} disabled={Loading} onClick={guardar}>
                      {Loading && <IonSpinner name="crescent" />}
                      {!Loading && <span>Guardar</span>}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default AdminStaffUpdate
